import Layout from '../components/Layout';

const Login = () => (
	<Layout>
		<section>
			<div className="container pt-3">
				<div className="row">
					<div className="col-md-4 offset-md-4">
						<div className="toggle-login">
							<div className="login">
								<h3 className="mb-4">Entrar</h3>
								
								<form method="post" id="loginform">
									<p class="login-username">
										<label htmlFor="user_login">Nome de usuário ou endereço de e-mail</label>
										<input type="text" name="log" id="user_login" class="input" size="20" />
									</p>

									<p class="login-password">
										<label htmlFor="user_pass">Senha</label>
										<input type="password" name="pwd" id="user_pass" class="input" size="20" />
									</p>
									
									<p class="login-remember">
										<label>
											<input name="rememberme" type="checkbox" id="rememberme" value="forever" /> Lembrar-me
										</label>
									</p>

									<p class="login-submit">
										<input type="submit" name="wp-submit" id="wp-submit" class="button button-primary" value="Acessar" />
									</p>
								</form>

								<hr class="ou my-4" />
								<div class="text-center">
									<a class="btn btn-lg btn-success show-register">Criar nova conta</a>
								</div>
							</div>

							<div className="register d-none">
								<h3 className="mb-4">Criar conta</h3>
								
								<form method="post">
									<div class="form-group">
										<label>Usuário</label>
										<input type="text" name="user_login" id="user_login" class="form-control alt" />
									</div>
									<div class="form-group">
										<label>E-mail</label>
										<input type="text" name="user_email" id="user_email" class="form-control alt" />
									</div>
									<p class="text-muted">Uma confirmação de registro será enviada para você por e-mail.</p>
											<input class="btn btn-lg btn-block btn-success" type="submit" value="Cadastre-se" id="register"/>

									<div class="text-center text-muted mt-4">
										Já tem uma conta? <a class="text-link text-primary show-login">Entre agora</a>
									</div>
								</form>
							</div>
						</div>
					</div>
				</div>
			</div>
		</section>
	</Layout>
);

export default Login;